import React, { Component } from 'react';
import { Email, Telefone } from '../../../Componentes';
import { AlunoCell } from '../../../Componentes/AlunoCell';
import { formatDateTimePtBr } from '../../../utils';

export default class ListarRequerimentos extends Component {
    constructor(){
        super()
        this.state = {
            listaDeRequerimentos : []
        }

        this.carregarListaDeRequerimentos = this.carregarListaDeRequerimentos.bind(this)
        this.apagarRequerimento = this.apagarRequerimento.bind(this)
    }

    componentWillMount(){
        this.carregarListaDeRequerimentos()
    }

    carregarListaDeRequerimentos(){
        const {api} = this.props
        api.Requerimentos.ListaRequerimentos(
            (listaDeRequerimentos) => this.setState({listaDeRequerimentos}),
            (erro) => console.log('erro',erro)
        )
    }

    linkParaBaixarRequerimento(id){
        const {api} = this.props
        return `${api.Requerimentos.getBaseUrl()}/${id}/baixar`
    }

    apagarRequerimento(id){
        const {api} = this.props
        api.Requerimentos.RemoveRequerimentoPelaId(id, 
            this.setState(prevState => {
                const listaDeRequerimentos = prevState.listaDeRequerimentos.filter(requerimento => requerimento.id !== id)
                return ({listaDeRequerimentos})
            }),

            (erro) => console.log('erro',erro)
        )
    }

    render() {
        const {listaDeRequerimentos} = this.state

        return (
            <div>
                <h5>Rela√ß√£o de Requerimentos</h5>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th></th>
                            <th colSpan={2}>Estagi√°rio</th>
                            <th>Local</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        listaDeRequerimentos.map(({aluno, local_do_estagio, orientador, supervisor, ...requerimento}, i) => {
                            const downloadLink = this.linkParaBaixarRequerimento(requerimento.id)
                            return (
                                <tr key={requerimento.id}>
                                    <td>
                                        {i+1}
                                    </td>
                                    <td>
                                        <AlunoCell aluno={aluno} />
                                        <div><small>Requerido em: {formatDateTimePtBr(new Date(requerimento.criado_em))}</small></div>
                                    </td>
                                    <td>
                                        <div><small>CPF: {aluno.cpf}</small></div>
                                        <div><small>Matr.: {aluno.matricula}</small></div>
                                    </td>
                                    
                                    <td>
                                        <div><small>Local: {local_do_estagio.nome} | <Telefone value={local_do_estagio.telefone}/> | {local_do_estagio.endereco.cidade} - {local_do_estagio.endereco.uf}</small></div>
                                        <div className="border-bottom"/>
                                        <div><small>Orientador: {orientador.nome}</small></div>
                                        <div className="border-bottom"/>
                                        <div><small>Supervisor: {supervisor.nome}</small></div>
                                        <div><small><Telefone value={supervisor.telefone}/> {supervisor.email && <> | <Email value={supervisor.email}/></>}</small></div>
                                    </td>
                                    <td>
                                        <button onClick={() => window.open(downloadLink, "_blank")}>Baixar</button>
                                        <button onClick={() => this.apagarRequerimento(requerimento.id)}>Apagar</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
