import React, { useState } from 'react'
import lockImg from "../../assets/svg/lock.svg"

export default function Login(props){
    const {api} = props

    const [loading, setLoading] = useState(false);
    const usuario = React.createRef()
    const senha = React.createRef()

    const onSubmit = (e) => {
        e.preventDefault()
        const username = usuario.current.value
        const password = senha.current.value

        if(username && password){
            setLoading(true);
            api.Auth.Login(username, password, () => setLoading(false), (e) => {
                setLoading(false)
            })
        }
    }

    if(api.Auth.accessToken() !== undefined)
        window.location.href = "#"

    return (
        <div className="d-flex align-items-center mx-auto" style={{minHeight:"100vh", maxWidth:"600px"}}>

            <form className="w-100 bg-white border rounded p-3" onSubmit={onSubmit}>
                
                <div class="text-center"><img src={lockImg} alt="lock" height={70} /></div>

                <div className="form-group">
                    <label>Usuario</label>
                    <input className="form-control" type="text" ref={usuario} required/>
                </div>

                <div className="form-group">
                    <label>Senha</label>
                    <input className="form-control" type="password" ref={senha} required/>
                </div>

                <button className="btn btn-primary" type="submit" disabled={loading}>
                    {loading ? "Entrando..." : "Entrar"}
                </button>
            </form>
        </div>
    )
}

export function Logout(props){
    const {api} = props
    api.Auth.Logout()

    return null
}